import React, { Component } from 'react'
import Card from './components/Card'
import './App.css'
import Search from './components/Search'
import Axios from 'axios'
import { ClipLoader } from 'react-spinners'
import Logo from './logo.svg'

export class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      movies: [],
      loading: false
    }
    this.getMovies = this.getMovies.bind(this)
  }

  async getMovies(moviesName) {
    const url = `https://www.omdbapi.com/?s=${moviesName}&page=1&apikey=f91c9872`
    this.setState({ loading: true })
    const response = await Axios.get(url)
    this.setState({ loading: false, movies: response.data.Search })
  }

  render() {
    return (
      <div className='App'>
        <img src={Logo} className='App-logo' alt='logo' />
        <Search getMovies={this.getMovies} />
        {this.state.loading ? (
          <ClipLoader className='' size={200} />
        ) : (
          <div className='App-Cards'>
            {this.state.movies === undefined ? (
              <div>Can't find movies</div>
            ) : this.state.movies.length === 0 ? (
              <div>You haven't search a movie..</div>
            ) : (
              this.state.movies.map(elem => (
                <Card
                  key={elem.imdbID}
                  title={elem.Title}
                  year={elem.Year}
                  img={elem.Poster}
                />
              ))
            )}
          </div>
        )}
      </div>
    )
  }
}

export default App
